<template>
  <div class="novel-tools">
    <RadioGroup type="button" v-model="type" class="tools-type" @on-change="changeType">
      <Radio :label="0">返回随机链接(支付宝链接)</Radio>
      <Radio :label="1">修改域名</Radio>
      <Radio :label="2">微跳按钮(淘口令)</Radio>
    </RadioGroup>
    <div class="back" v-if="type == 0">
      <div class="alipay">
        <Form :model="form" class="form-group" label-position="right" :label-width="100">
          <FormItem label="链接标题">
            <Input size="large" type="text" v-model="form.name" placeholder="请输入链接标题"></Input>
          </FormItem>
          <FormItem label="链接">
            <Input size="large" type="text" v-model="form.link" placeholder="请输入链接"></Input>
          </FormItem>
          <FormItem>
            <Button size="large" type="success" @click="addOne">添加一条</Button>
          </FormItem>
        </Form>
        <Table class="novel-table" :columns="linkHeader" :data="linkList"></Table>
      </div>
    </div>
    <div class="domain" v-else-if="type == 1">
      <Input size="large" type="text" v-model="domain_name" placeholder="请输入域名"></Input>
      <Button class="edit-btn" size="large" type="success" @click="update_domain">修改域名</Button>
    </div>
    <div class="token" v-else>
      <Input size="large" type="text" v-model="tokenArr" placeholder="请输入淘口令"></Input>
      <Button class="edit-btn" :disabled="tokenArr == ''" size="large" type="success" @click="updateTokens">更新淘口令</Button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      type: 0,
      form: {},
      linkList: [],
      domain_names: [],
      tokenArr: "",
      domain_name: "",
      linkHeader: [
        {
          key: 'name',
          title: '链接标题',
          align: 'center'
        }, {
          key: 'link',
          title: '链接',
          align: 'center'
        }, {
          title: 'Action',
          align: 'center',
          render: (h, params) => {
            let that = this;
            return h('Button', {
              props: {
                type: 'error',
                size: 'small'
              },
              on: {
                click() {
                  that.deleteOne(params.row, params.index)
                }
              }
            }, '删除')
          }
        }
      ]
    };
  },
  mounted() {
    this.showList();
  },
  methods: {
    changeType() {
      this.form = {}
      this.domain_name = this.tokenArr = ''
    },
    addOne() {
      this.$axios.post('/alipayLink', this.form)
      .then(res => {
        this.linkList.push(this.form);
        this.$Message.info(res.data.success);
        this.form = {};
      });
    },
    showList() {
      this.$axios.get('/alipayLink')
      .then(res => {
        this.linkList = res.data.data;
      });
    },
    deleteOne(row, index) {
      this.$Modal.confirm({
        title: '提示:',
        content: '确认删除这条内容吗？',
        onOk: () => {
          this.$axios.get('/alipayLink/del', {params: { id: row._id }})
          .then(res => {
            if(res.data.success) {
              this.$Message.info(res.data.success);
              this.linkList.splice(index, 1);
            }else {
              this.$Message.info(res.data.error + ', 请刷新后重试');
            }
            
          });
        }
      })
    },
    updateTokens() {
      this.$axios.get("/tuiguang/token_arr", { params: { tokenArr: this.tokenArr } })
      .then(res => {
        this.$Message.info(res.data.success);
        this.tokenArr = "";
      });
    },
    update_domain() {
      if (this.domain_name == "") {
        this.$Message.info("域名不能为空，请重新填写！！！");
      } else {
        this.getId()
        this.$axios.get('/transfer/update_links', {
          params: {
            id: this.domain_names[0]._id,
            domain_name: this.domain_name
          }
        }).then(res => {
          this.$Message.info(res.data.success);
          this.domain_name = "";
        });
      }
    },
    getId() {
      this.$axios.get('/transfer')
      .then(res => {
        this.domain_names = res.data.domain_names;
      });
    }
  }
};
</script>

<style scoped>
.novel-tools {
  width: 800px;
  text-align: left;
  margin: 0 auto;
}

.tools-type {
  padding: 50px 80px;
  margin-left: 30px;
}
.form-item {
    float: left;
    width: 400px;
}
.form-item:last-child {
    width: 200px;
    margin-left: -50px;
}
.domain, .token {
  width: 500px;
  margin-left: 100px;
}

.edit-btn {
  margin: 30px 0;
}
</style>
